import React from 'react';
import './App.css';
import { Switch, HashRouter } from 'react-router-dom';
import AppUa from './AppUa/AppUa'
import AppUk from './AppUk'
import AppRu from './AppRu/AppRu';

const App = () => {
  return (

    <Switch>
      <HashRouter path="/ru" component={AppRu} />
      <HashRouter path="/ua" component={AppUa} />
      <HashRouter path="/" component={AppUk} />
      
    </Switch>
  );
}

export default App;
